
@media (max-width: 700px) {
.ModalBackground {
    z-index: 1;
    background-color: rgba(32, 35, 41, 0.8);
    display: flex;
    justify-content: center;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
}

.quitButton {
    z-index: 1;
    border-radius: 50%;
    height: 50px;
    width: 50px;
    align-items: center;
    font-size: 20px;
    color: white;
    position: fixed;
    bottom: 25px;
    right: 25px;
}

.quitButton:hover {
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.4); /* Sombra más profunda */
    transition: box-shadow 0.3s ease;
}

}