.searchInput {
    margin-top: 10px;
    align-self: center;
    border-radius: 6px;
    border: transparent;
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.3); /* Sombra más profunda */
    transition: box-shadow 0.3s ease;
    padding: 10px;
    width: 12vw;
    background-color: transparent;
}

.searchInput:hover {
 box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4); /* Efecto más marcado al pasar el mouse */   
}


@media (max-width: 700px) {
    .searchInput {
        width: 78vw;
        
    }
}