
@media (min-width: 701px) {
#main {
    display: flex;
    flex-direction: column;
    margin: 40px;
    border-radius: 6px;
    border: transparent;
    width: 500px;
    height: 800px;
    background-color: transparent;
    text-align: start;
    justify-content: flex-start;
    margin-left: 15vw;
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.4); /* Sombra más profunda */
    transition: box-shadow 0.3s ease;
    font-family: 'poppins', sans-serif;
    background-image: url('https://miro.medium.com/v2/resize:fit:1200/1*5AD_-gIiGKC2gzpKe6R9eQ.png');
    background-repeat: no-repeat;
    background-position: left bottom; /* Posiciona la imagen en la esquina inferior izquierda */
    background-size: contain; /* Ajusta el tamaño de la imagen sin recortarla */

}

#main:hover {
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.5); /* Efecto más marcado al pasar el mouse */   
}

#main h1 {
    display: flex;
    font-size: 40px;
    margin-bottom: 0;
    margin-top: 50px;
    margin-left: 30px;
}

#main label {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 30px;
    

}

#main button {
    background: #673AB7;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #512DA8, #673AB7);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #512DA8, #673AB7); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    margin-top: 10px;
}

#main button:hover {
    transform: scale(1.05);
    transition: 0.2s ease;
}


#main input {
    background-color: rgba(166, 164, 164, 0.4);;

    border-color:  #9CA3AF;
    padding: 11px;
    border-radius: 5px;
    border: transparent;
    width: 85%;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.4);
    
}
}


    
    @media (min-width: 701px) {
        form {
            display: none;
        }

    }

@media (max-width: 700px) {
.main.open {
    position: absolute;
    background-color: rgba(32, 35, 41, 0.8);
    height: 100%;
    width: 100%;
    z-index: 1;
    
}
}

    
@media (max-width: 700px) {
    #main {
        position: absolute;
        
        height: 100%;
        width: 100%;
        
    }
    .TodoMainForm {
        position: absolute;
        top: 30vh;
        left: 25vw;
        bottom: 30vh;
        right: 25vw;
        z-index: 1;
        background-color: rgba(32, 35, 41, 0.8);
    }
    
    #main h1 {
        display: none;
    }
    .rootLabel {
        display: none !important;
    }

    .ModalBackground {
        z-index: 1;
        background-color: rgba(32, 35, 41, 0.8);
        display: flex;
        justify-content: center;
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        top: 0;
    }
    
    

    
    }