@media (min-width: 601px) {
    .MobileTaskButton {
        display: none;
    }
}

@media (max-width: 600px) {
    .MobileTaskButton {
        background: linear-gradient(45deg, #6a11cb, #2575fc);
        width: 50px;
        height: 50px;
        justify-self: center;
        color: white;
        border: none;
        padding: 10px;
        border-radius: 50%;
        font-size: 24px;
        cursor: pointer;
        margin-top: 10px;
        align-self: center;
        
        position: fixed;
        bottom: 20px;
        margin-left: 85%;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3); /* Sombra inicial */
        transition: box-shadow 0.3s ease, background 0.3s ease;
        z-index: 2;
    }

    .MobileTaskButton:hover {
        background: linear-gradient(45deg, #8e2de2, #4a00e0); /* Degradado más brillante */
        box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.4); /* Sombra más intensa */
        transform: scale(1.1); /* Efecto de agrandado */
        transition: transform 0.3s ease, background 0.3s ease;
    }
}


