.skeleton-list {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .skeleton-item {
    height: 20px;
    background-color: #e0e0e0; /* Color base del skeleton */
    border-radius: 4px;
    margin-bottom: 10px;
    position: relative;
    overflow: hidden;
  }
  
  .skeleton-item::before {
    content: '';
    position: absolute;
    top: 0;
    left: -150%;
    height: 100%;
    width: 150%;
    background: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0.2) 50%, rgba(255,255,255,0) 100%);
    animation: loading 1.5s infinite;
  }
  
  @keyframes loading {
    0% {
      left: -150%;
    }
    100% {
      left: 150%;
    }
  }
  