.TodoTitle {
    margin: 0;
    padding: 32px;
    text-align: center;
    font-size: 50px;
    
    background: rgb(63,94,251);
    background: radial-gradient(circle, rgba(63,94,251,1) 0%, rgba(253,64,102,1) 100%);
    background-clip: text;
    color: transparent; /* Asegurarse de que el texto también sea transparente en otros navegadores */
    font-family: "Roboto", sans-serif;
    font-weight: 900;
    font-style: italic;
    display: none;
}



@media (max-width: 700px) {
    .TodoTitle {
        display: block;
        text-align: center;
    }
}