
@media (max-width: 700px) {
    

form {
    display: grid;
    place-items: center;
    background-color: rgba(26, 7, 7, 0.1);
    
    border-radius: 15px;
    padding: 20px;
    width: auto;
    height: auto;
    margin: 0 auto;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    
}

.label-title {
    display: flex;
    justify-content: center;
    margin-bottom: 5px;
    font-size: 24px;
    color: aliceblue;
}

.TodoButtonContainer {
    display: flex;
    justify-content: space-between;
}

.TodoButtonContainer button {
    width: 15vw;
    padding: 10px 10px;
    margin: 10px;
    border-radius: 5px;
    border: none;
    font-size: 12px;
    color: whitesmoke;
}

.TodoButtonContainer button:nth-child(2) {
    background: linear-gradient(45deg, #6a11cb, #2575fc);
}
.TodoButtonContainer button:nth-child(1) {
    background-color: rgb(170, 48, 48);
}

.text-area {
    
    height: 121px;
    background-color: rgba(255, 255, 255, 0.1);
    resize: none;
}
}


@media (min-width: 701px) {
    form {
        display: none;
    }
}